import React from 'react';
import './App.css';

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {//estado do componente
      //criacao de variaveis
      contador: 0,
      nome:" ",
      ano: 0,
      msg: ""
    }
  }
  handleAperte = (inc) => {
    this.setState((state) => ({ contador: state.contador + inc }))//primeiro fornece o stado e depois altera
    // não fazer this.setState((state) => ({contador: this.state.contador + 1})
  }
  zera = () => {
    this.setState({ contador: 0 })//alterando estado
  }
  handleInput = (event) => {
    const name = event.target.name
    this.setState({[name]: event.target.value})
  }
  handleForm= () => {
    this.setState((state) => ({msg: `Olá ${state.nome}! Sua idade em 2021 é ${2021-state.ano}`}))
  }
  render() {
    return (
      <div className="App">
        <h1>Olá</h1>
        <p>contador = {this.state.contador} </p>
        <p><button onClick={() => this.handleAperte(1)}>Aumenta</button></p>
        <p><button onClick={() => this.handleAperte(-1)}>Diminui</button></p>
        <p><button onClick={this.zera}>Zera</button></p>
        <p>Aqui também aparece: {this.state.contador} </p>
        <p>O dobro é: {this.state.contador * 2} </p>
        {this.state.contador > 5 && <p>Passou de 5</p>}
        <div>
          <p>Qual o seu nome?</p>
          <input name="nome" value={this.state.nome} onChange={this.handleInput} />
          <input name="ano" value={this.state.ano} onChange={this.handleInput} />
          <button onClick={this.handleForm}>OK</button>
          <p>{this.state.msg}</p>
        </div>
      </div>

    );
  }
}

export default App;
